import React from "react";
import Subject from "./Subject";
import './Day.css';
import * as firebase from "firebase";
import "firebase/database";

export default class Day extends React.Component {

    onSubjectChange(subjectKey, subjectTitle, newText) {
        const { dayName } = this.props;

        // console.log('### --------------------- onTextChanged ', );
        // console.log('### dayName = ', dayName.toLowerCase());
        // console.log('### subjectKey = ', subjectKey);
        // console.log('### subjectTitle = ', subjectTitle);
        // console.log('### newText = ', newText);

        firebase.database().ref('/week1/' + dayName.toLowerCase() + '/' + subjectKey).set({
            title: subjectTitle,
            text: newText,
        }, (error) => {
            if (error) {
                console.log('### The write failed...', );
            } else {
                console.log('### Data saved successfully! ', newText);
            }
        });
    }

    render() {
        const { day, dayName } = this.props;
        if (!day) return null;
        const subjectsKeys = Object.keys(day); // TODO: sort
        return (
            <div className="Day">
                <div className="DayName">
                    {dayName}
                </div>
                <div>
                    {subjectsKeys && subjectsKeys.map((subjectKey) =>
                        <Subject subject={day[subjectKey]}
                                 key={day[subjectKey].title+subjectKey}
                                 onSubmit={(newText)=>{
                                     this.onSubjectChange(subjectKey, day[subjectKey].title, newText);
                                 }}
                        />
                    )}
                </div>
            </div>
        );
    }
}
