import React from "react";
import Day from "./Day";
import './Week.css';
export default class Week extends React.Component {

    render() {
        const { week } = this.props;
        const { montag, dienstag, mittwoch, donnerstag, freitag } = week; // && week.length > 0 && week['Montag'];
        // console.log('### week = ', week);
        // console.log('### montag = ', montag);
        // console.log('### dienstag = ', dienstag);
        // console.log('### dienstag = ', dienstag);
        // console.log('### dienstag = ', dienstag);
        // console.log('### dienstag = ', dienstag);
        return (
            <div className="Week">
                {montag && <Day key={'montag'} day={montag} dayName={'Montag'} />}
                {dienstag && <Day key={'dienstag'} day={dienstag} dayName={'Dienstag'} />}
                {mittwoch && <Day key={'mittwoch'} day={mittwoch} dayName={'Mittwoch'} />}
                {donnerstag && <Day key={'donnerstag'} day={donnerstag} dayName={'Donnerstag'} />}
                {freitag && <Day key={'freitag'} day={freitag} dayName={'Freitag'} />}
            </div>
        );
    }
}
