import React from 'react';
import logo from './loga.png';
import './App.css';
import * as firebase from "firebase/app";
import "firebase/database";
import Week from "./Week";
import info from './info.png';

var firebaseConfig = {
    apiKey: "AIzaSyD4Olvz6xCJbKhukyXDtH0iofl1mwPkavo",
    authDomain: "homebooks-eintrick.firebaseapp.com",
    databaseURL: "https://homebooks-eintrick.firebaseio.com",
    projectId: "homebooks-eintrick",
    storageBucket: "homebooks-eintrick.appspot.com",
    messagingSenderId: "218188571515",
    appId: "1:218188571515:web:4ec3d2cdce8e70e92613de"
};

firebase.initializeApp(firebaseConfig);
const databaseRef = firebase.database().ref();
const week1Ref = databaseRef.child("week1");

// export const todosRef = database.child("homebook");


class App extends React.Component {

    state = {
        week1: []
    };

    componentDidMount() {
        week1Ref.on("value", snapshot => {
            const week1 = snapshot.val();
            // console.log('### week1 = ', week1);
            this.setState({week1})
        });
    }

    render() {
        const { week1 } = this.state;

        return (
            <div className="App">
                <header className="App-header">
                    <div className="LogoContainer">
                        <img src={logo} className="App-logo" alt="logo" />
                        <img src={info} className="Info-icon" alt="info" />
                    </div>
                    <Week week={week1}/>
                </header>
            </div>
        );
    }
}



export default App;
